<template>
    <div class="allOrder">
        <div class="container">
            <!-- 表头 -->
            <ul class="infoBar">
                <li><p>商品信息</p></li>
                <li>单价</li>
                <li>数量</li>
                <li>实付款</li>
                <li>订单状态</li>
            </ul>

            <!-- 表格内容 -->
            <ul class="infoContent">
                <li v-for="item in orderList" :key="item.id">
                    <div class="itemTop">
                        <p>订单编号：&nbsp;{{ item.order_id }}</p>
                        <p>
                            下单时间：
                            {{
                                item.createdAt
                                    .replace("T", " ")
                                    .replace(".000Z", " ")
                            }}
                        </p>
                    </div>
                    <ul class="itemDetail">
                        <li v-for="item in goodsList" :key="item.id">
                            <img src="" alt="" />
                        </li>
                        <li>
                            <p>{{ item.goods_name }}</p>
                        </li>
                        <li><p>¥749.00</p></li>
                        <li><p>1</p></li>
                        <li><p>¥749.00</p></li>
                        <li><p>已支付</p></li>
                    </ul>
                </li>
            </ul>
        </div>

        <!-- 分页器 -->
        <div class="block">
            <el-pagination
                :page-size="10"
                layout="prev, pager, next, jumper"
                :total="20"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            orderList: [],
            goodsList: [],
        };
    },
    created() {
        // 获取订单信息
        axios({
            method: "GET",
            url: "http://shop.bluej.cn/api/order",
            params: {
                project_id: 90,
            },
            headers: {
                "x-token": this.$store.state.token,
            },
        })
            .then((res) => {
                this.orderList = res.data.result.rows;
                if (this.orderList.length) {
                    console.log(this.orderList);
                    // 判断获取的商品有无
                    // this.isCar = true;
                    // 获取订单商品信息
                    Object.keys(this.orderList).forEach((key) => {
                        let item = this.orderList[key].goods_info;
                        item = JSON.parse(item);
                        this.goodsList.push(item);
                    });
                    console.log(this.goodsList);
                } else {
                    // this.isCar = false;
                }
            })
            .then((err) => {
                // console.log(err);
            });
    },
};
</script>

